import React from 'react'
import {Redirect} from 'react-router-dom'

const Gloves = ({gloves}) => {
    if(gloves.lenght === 0) {
        return (
            <Redirect to="/" />
        )
    }
    return(
        <div id='gloves'>
            {gloves.map(glove =>
            <div>{glove.name}</div>)

            }
        </div>
    )
}

export default Gloves