import React from 'react'
import {Redirect} from 'react-router-dom'

const Beanies = ({beanies}) => {
    if(beanies.lenght === 0) {
        return (
            <Redirect to="/" />
        )
    }
    return (
        <div id='beanies'>
            {beanies.map(beanie =>
                <div>{beanie.name}</div>
            )
            }
        </div>
    )
}

export default Beanies