import React from 'react'
import {Redirect} from 'react-router-dom'

const Facemasks = ({facemasks}) => {
    if(facemasks.lenght === 0) {
        return (
            <Redirect to="/" />
        )
    }
    return(
        <div id='facemasks'>
            {facemasks.map(mask =>
            <div>{mask.name}</div>)

            }
        </div>
    )
}

export default Facemasks