import React, { useState, useEffect } from 'react'
import {
  BrowserRouter as Router,
  Switch, Route, Link
} from 'react-router-dom'
import itemService from './services/items'
import Gloves from './components/gloves'
import Facemasks from './components/facemasks'
import Beanies from './components/beanies'
import './App.css';

const App = () => {
  const [gloves, setGloves] = useState([])
  const [beanies, setBeanies] = useState([])
  const [facemasks, setFacemasks] = useState([])
  const padding = {
    padding: 5
  }


  useEffect(() => {
    async function renderGloves() {
      const data = await itemService.getGloves()
      setGloves(data)
    }
    async function renderFacemasks() {
      const data = await itemService.getFacemasks()
      setFacemasks(data)
    }
    async function renderBeanies() {
      const data = await itemService.getBeanies()
      setBeanies(data)
    }
    renderGloves()
    renderFacemasks()
    renderBeanies()
  }, [])

  console.log(gloves.length);
  console.log(beanies.length);
  console.log(facemasks.length);
  return (
    <Router>
      <div>
        <Link style={padding} to="/"> home </Link>
        <Link style={padding} to="/gloves"> gloves </Link>
        <Link style={padding} to="/facemasks"> facemasks </Link>
        <Link style={padding} to="/beanies"> beanies </Link>
      </div>
      <Switch>
        <Route path="/gloves">
          <Gloves gloves={gloves} />
        </Route>
        <Route path="/facemasks">
          <Facemasks facemasks={facemasks} />
        </Route>
        <Route path="/beanies">
          <Beanies beanies={beanies} />
        </Route>
        <Route path="/">
          <div>
            Home
          </div>
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
